@media screen and (min-width: 992px) and (max-width: 1199px) {
	body.theme-@{index} {
		.team-member-wrapper {
			margin-bottom: 50px;
		}
	}
}
@media screen and (min-width: 768px) and (max-width: 991px) {
	body.theme-@{index}
	{
		.team-member-wrapper {
			margin-bottom: 50px;
		}
		.team-member {
			width: 80%;
			margin: 0 10%;
		}
		.pricing-box {
			width: 70%;
			margin: 0 auto 30px;
			float: none;
		}
	}
}
@media screen and (min-width: 200px) and (max-width: 767px) {
	html {
		font-size: 14px;
	}
	body.theme-@{index} {
		.team-member-wrapper {
			margin-bottom: 50px;
		}
		.team-member {
			width: 40%;
			margin: 0 30%;
		}
		.elements-container {
			.nav-tabs {
				border: none;
				margin-bottom: 40px;
				li {
					width: auto;
					a {
						margin-left: 0;
						.perl-icon {
							margin-right: 5px;
						}
					}
				}
			}
			.tabs {
				.nav-tabs {
					margin-bottom: 0;
				}
			}
		}
		.newsletter {
			span {
				.set-default;
				margin-bottom: 20px;
			}
			input.form-control {
				.set-default;
				margin-bottom: 20px;
			}
			button {
				left: 50%;
				margin-left: -40px;
			}
		}
	}
}

@media screen and (min-width: 200px) and (max-width: 600px) {
	body.theme-@{index} {
		.team-member {
			width: 50%;
			margin: 0 25%;
		}
	}
}

@media screen and (min-width: 200px) and (max-width: 500px) {
	body.theme-@{index} {
		.team-member {
			width: 70%;
			margin: 0 15%;
		}
	}
}

@media screen and (min-width: 200px) and (max-width: 400px) {
	body.theme-@{index} {
		.team-member {
			width: 80%;
			margin: 0 10% 50px;
		}
	}
}