@import "animate.less";
@import "entypo.less";
@import "isotope.less";
@import "jquery.bxslider.less";
@import "lesshat.less";
@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,800,300|Dosis:200,300,400,500,600,700,800|Exo:100,200,300,400,500,600,700,800,900,100italic,200italic,300italic,400italic,500italic,600italic,700italic,800italic,900italic);

@dark-color: #A6469F;
@light-color: #F2D7D0;
@background-color: white;
@numberOfThemes: 3;

@alternate-color: #F44472;
@alternate-color-2: #BF4E8C;
@alternate-color-3: #F2DA91;
@alternate-color-4: #d58512;

@text-dark: #252525;
@text-light: #929292;
@menu-active: #ddd;

.themeDefs(1) {
	@dark-color: #7946bd;
	@light-color: #927CBF;

	@background-color: #FDFDFD;

	@alternate-color: #F44472;
	@alternate-color-2: #BF4E8C;
	@alternate-color-3: #F2DA91;
	@alternate-color-4: #d58512;

	@menu-active: white;
}

.themeDefs(2) {
	@dark-color: #F44E4E;
	@light-color: #F44E4E;

	@background-color: #FDFDFD;

	@alternate-color: #E5B96F;
	@alternate-color-2: #CC2738;
	@alternate-color-3: #F2DA91;
	@alternate-color-4: #690011;

	@menu-active: #ddd;
}

.themeDefs(3) {
	@dark-color: #C13938;
	@light-color: #C13938;
	@background-color: #FDFDFD;
	@alternate-color: #E5B96F;
	@alternate-color-2: #CC2738;
	@alternate-color-3: #F2DA91;
	@alternate-color-4: #690011;

	@menu-active: #ddd;
}
html {
	font-size: 16px;
}
.set-default {
	float: left;
	width: 100%;
}

.font(@font: 'Arial, sans-serif', @size, @color, @weight: 400) {
	font-family: @font;
	font-size: @size;
	color: @color;
	font-weight: @weight;
}

.buildThemes(@index) when (@index < (@numberOfThemes + 1)) {
	body.theme-@{index} {
		z-index: 0;
		#theme-swatch {
			.transition(all .4s ease-in-out);
			position: fixed;
			left: -160px;
			top: 300px;
			background: white;
			z-index: 50;
			padding: 20px;
			text-align: center;
			.box-shadow(0px 1px 5px rgba(0,0,0,0.3));
			h4 {
				font-family: "Open Sans", "Arial, sans-serif";
				font-size: 1rem;
				color: @text-light;
				font-weight: 300;
				margin: 0 0 15px;
			}
			.colors {
				margin: 0;
				padding: 0;
				list-style: none;
				text-align: center;
				li {
					margin: 0 5px;
					float: left;
				}
				.color-swatch {
					width: 30px;
					height: 30px;
					//border: solid 2px transparent;
					.box-shadow(none);
					.transition(all .2s ease-in-out);
					display: block;
					&.active {
						//border: solid 2px @light-color;
						.box-shadow(3px 2px 1px rgba(0, 0, 0, 0.4));
					}
					&.purple {
						background: #A6469F;
					}
					&.red {
						background: #F44E4E;
					}
					&.blue {
						background: blue;
					}
				}
			}
			.chooser {
				width: 192px;
				background: @text-light;
				color: white;
				position: absolute;
				top: -2rem;
				left: 0;
				z-index: 51;
				font-family: "Open Sans", "Arial, sans-serif";
				font-weight: 300;
				font-size: 1rem;
				line-height: 2rem;
				padding-left: 20px;
				padding-right: 5px;
				.border-radius(0 4px 4px 0);
				span {
					float: left;
				}
				i {
					color: white;
					vertical-align: middle;
					font-size: 1rem;
					float: right;
				}
			}
		}
		.background-image(linear-gradient(top, #311c4e 0%, #895FA7 30%));
		font-family: 'Open Sans', Arial, sans-serif;
		font-size: 0.9375rem;
		font-weight: 400;
		.themeDefs(@index);
		.transition(background .3s ease-in-out);
		position: relative;
		&.no-perspective {
			.perspective(0px);
		}
		&.with-perspective {
			.perspective(1500px);
			overflow: hidden;
		}
		&.effect-movedown {
			.background-image(linear-gradient(top, #311c4e 0%, #895FA7 30%));
			width: 100%;
			height: 100%;
			.navbar-default {
				.opacity(0);
			}
			main {
				background: @background-color;
				.transform(scale(0.8) rotate3d(1, 0, 0, 40deg) translateY(7%));
//				.transform(scale(1.5) rotate3d(1, 0, 0, 40deg) translateY(20%));
			}
			.login-combo {
				top: 40px;
			}
		}
		main {
			.set-default;
			background: @background-color;
			position: relative;
			.transform-origin(50% 50%);
			.transform(scale(1) rotate3d(1, 0, 0, 0deg) translateY(0%));
			.transition(transform .3s ease-in-out);
		}
		.heading-poperties {
			margin: 0 0 25px;
			font-family: 'Dosis', Arial, sans-serif;
			font-weight: 300;
			color: @text-dark;
		}
		.login-combo {
			position: absolute;
			top: -1000px;
			left: 0;
			width: 100%;
			.transition(all .3s ease-in-out);
			.half {
				padding-top: 20px;
				padding-bottom: 20px;
				float: left;
				width: 50%;
				padding-left: 50px;
				border-left: solid 1px @background-color;
				margin-left: -1px;
				&:first-child {
					padding-left: 0;
					padding-right: 50px;
					border-left: 0;
					margin-left: 0px;
					border-right: solid 1px @background-color;
				}
			}
			h2 {
				color: white;
				text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
			}
			.input-group {
				margin-bottom: 0;
				background: transparent;
			}
			.input-group-addon {
				color: white;
				border-bottom: solid 1px white;
			}
			.form-control {
				font-family: 'Dosis', Arial, sans-serif;
				font-size: 1rem;
				padding-top: 12px;
				padding-bottom: 12px;
				height: 60px;
				padding-left: 0;
				text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
				border-bottom: solid 1px @background-color;
				background: transparent;
				color: white;
				.placeholder(white);
			}
			.input-line {
				margin-top: 30px;
			}
			.btn-block {
				margin-top: 50px;
			}
		}
		.input-group-addon {
			background: transparent;
			color: @light-color;
			border-top: none;
			border-left: none;
			.border-radius(0);
			border-bottom: solid 1px @light-color;
		}
		.input-group {
			margin-bottom: 10px;
			background: transparent;
		}
		.form-control {
			background: transparent;
			border: none;
			border-bottom: solid 1px @light-color;
			font-weight: 300;
			.placeholder(@text-light);
			.border-radius(0);
			.box-shadow(none);
			padding-top: 12px;
			padding-bottom: 12px;
			height: 42px;
			color: @text-light;
			.set-default;
		}
		textarea.form-control {
			.set-default;
			margin-top: 20px;
			height: 200px;
			resize: none;
			background: transparent;
			margin-bottom: 10px;
			border-bottom: solid 1px @light-color;
			border-top: dashed 1px lighten(@light-color, 30%);
			border-left: dashed 1px lighten(@light-color, 30%);
			border-right: dashed 1px lighten(@light-color, 30%);
		}
		h1 {
			font-size: 3rem;
			.heading-poperties;
		}
		h2 {
			font-size: 2.3rem;
			.heading-poperties;
		}
		h3 {
			font-size: 1.7rem;
			.heading-poperties;
		}
		h4 {
			font-size: 1.2rem;
			.heading-poperties;
		}
		h5 {
			font-size: 1rem;
			.heading-poperties;
		}
		h6 {
			font-size: 0.9rem;
			.heading-poperties;
		}
		.btn {
			.border-radius(8px 0 8px 0);
			border: none;
			outline: none;
			font-family: "Open Sans", Arial, sans-serif;
			font-size: 1rem;
			font-weight: 300;
			color: white;
			padding: 8px 15px;
			border: none;
			cursor: pointer;
			display: inline-block;
			outline: none;
			position: relative;
			.transition(all .4s ease-in-out);
			&:after {
				content: '';
				position: absolute;
				z-index: -1;
			}
			&:hover {
				.opacity(0.7);
			}
			&.btn-xs {
				font-size: 0.8rem;
				padding: 5px 10px;
			}
			&.btn-lg {
				padding: 10px 20px;
				font-size: 1.5rem;
			}
			&.btn-primary {
				background: @light-color;
				&.btn-empty {
					border: solid 2px @light-color;
					background: transparent;
					color: @light-color;
					&:hover {
						background: @light-color;
						color: white;
					}
				}
			}
			&.btn-success {
				background: @alternate-color-2;
				&.btn-empty {
					border: solid 2px @alternate-color-2;
					background: transparent;
					color: @alternate-color-2;
					&:hover {
						background: @alternate-color-2;
						color: white;
					}
				}
			}
			&.btn-info {
				background: @alternate-color-3;
				&.btn-empty {
					border: solid 2px @alternate-color-3;
					background: transparent;
					color: @alternate-color-3;
					&:hover {
						background: @alternate-color-3;
						color: white;
					}
				}
			}
			&.btn-warning {
				background: @alternate-color-4;
				&.btn-empty {
					border: solid 2px @alternate-color-4;
					background: transparent;
					color: @alternate-color-4;
					&:hover {
						background: @alternate-color-4;
						color: white;
					}
				}
			}
			&.btn-danger {
				background: @alternate-color;
				&.btn-empty {
					border: solid 2px @alternate-color;
					background: transparent;
					color: @alternate-color;
					&:hover {
						background: @alternate-color;
						color: white;
					}
				}
			}
			&.btn-icon {
				&.btn-block {
					&:after {
						right: -120px;
					}
				}
				&:before {
					font-family: 'entypo';
					speak: none;
					line-height: 1;
					position: absolute;
					height: 100%;
					top: 0;
					line-height: 2.5;
					font-size: 140%;
					width: 60px;
					right: -10px;
					z-index: 2;
				}
				&:after {
					width: 40%;
					height: 200%;
					background: rgba(255,255,255,0.1);
					z-index: 1;
					right: 0;
					top: 0;
					margin: -5px 0 0 -5px;
					-webkit-transform-origin: 0 0;
					-webkit-transform: rotate(-20deg);
					-moz-transform-origin: 0 0;
					-moz-transform: rotate(-20deg);
					-ms-transform-origin: 0 0;
					-ms-transform: rotate(-20deg);
					transform-origin: 0 0;
					transform: rotate(-20deg);
					.transition(all .4s ease-in-out);
				}
				&:hover{
					.opacity(1);
					&:after {
						width: 50%;
					}
				}
				padding: 15px 70px 15px 20px;
				overflow: hidden;

			}

		}
		.navbar-default {
			background: transparent;
			border-color: transparent;
			padding-bottom: 0;
			margin-bottom: 0;
			.opacity(1);
			.background {
				background: white;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				.box-shadow(0px 1px 5px rgba(0,0,0,0.3));
				.opacity(0);
			}
			.navbar-brand {
				color: @text-dark;
				font-size: 2.5rem;
				font-family: 'Dosis', Arial, sans-serif;
				font-weight: 400;
				.transition(all .3s ease-in-out);
				i {
					.transition(all .3s ease-in-out);
					font-size: 2.5rem;
					color: @dark-color;
				}
			}
			&.transparent-background {
				.navbar-brand {
					color: white;
					font-size: 3.5rem;
					text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
					i {
						font-size: 3.5rem;
						color: white;
					}
				}
				.navbar-nav {
					&> li {
						&>a {
							padding-top: 30px;
							color: white;
							text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
						}
						&.active {
							&>a {
								color: @menu-active;
							}
						}
					}
				}
			}
		}
		.navbar-nav {
			&> li {
				&>a {
					.transition(all .3s ease-in-out);
					overflow: hidden;
					position: relative;
					font-size: 1.1rem;
					padding-top: 20px;
					&:before {
						position: absolute;
						bottom: -5px;
						left: -100px;
						content: "\e8ed";
						font-family: "entypo";
						margin-left: -13px;
						.transition(left .3s ease-in-out);
					}
					&:after {
						position: absolute;
						bottom: -5px;
						right: -100px;
						content: "\e8ed";
						font-family: "entypo";
						margin-right: -13px;
						.transition(right .3s ease-in-out);
					}
					&:hover {
						&:before {
							left: 50%;

						}
						&:after {
							right: 50%;
						}
					}
				}
				&.active {
					&>a {
						position: relative;
						background: transparent;
						color: @dark-color;
						&:hover {
							background: transparent;
						}
						&:before {
							left: 50%;

						}
						&:after {
							right: 50%;
						}
					}
				}
			}
		}
		.pearl-icon {
			display: inline-block;
			text-align: center;
			color: @light-color;
			.transition(all .4s ease-in-out);
			&.lg-icon {
				width: 90px;
				height: 90px;
				font-size: 3rem;
				line-height: 82px;
			}
			&.md-icon {
				width: 60px;
				height: 60px;
				font-size: 2rem;
				line-height: 52px;
			}
			&.sm-icon {
				width: 40px;
				height: 40px;
				font-size: 1rem;
				line-height: 35px;
			}
			&.dark {
				color: @dark-color;
			}
			&.alternate {
				color: @alternate-color;
			}
			&.alternate-2 {
				color: @alternate-color-2
			}
			&.alternate-3 {
				color: @alternate-color-3;
			}
			&.empty-icon {
				border: solid 2px @light-color;
				background: transparent;
				.border-radius(50%);
				&.dark {
					border: solid 2px @dark-color;
				}
				&.alternate {
					border: solid 2px @alternate-color;
				}
				&.alternate-2 {
					border: solid 2px @alternate-color-2
				}
				&.alternate-3 {
					border: solid 2px @alternate-color-3;
				}
				&:hover {
					background: @light-color;
					color: white;
					&.dark {
						background: @dark-color;
					}
					&.alternate {
						background: @alternate-color;
					}
					&.alternate-2 {
						background: @alternate-color-2
					}
					&.alternate-3 {
						background: @alternate-color-3;
					}
				}
			}
			&.fill-icon {
				background: @light-color;
				color: white;
				.border-radius(50%);
				&.dark {
					background: @dark-color;
				}
				&.alternate {
					background: @alternate-color;
				}
				&.alternate-2 {
					background: @alternate-color-2
				}
				&.alternate-3 {
					background: @alternate-color-3;
				}
				&:hover {
					.opacity(0.7);
				}
			}
			&.fb-icon:hover {
				color: #4c66a4;
			}
			&.twitter-icon:hover {
				color: #4099FF;
			}
			&.googleplus-icon:hover {
				color: #D34836;
			}
		}
		.line {
			.set-default;
			padding: 50px 0;
			h1 {
				font-weight: 400;
				.set-default;
				text-align: center;
				margin-bottom: 60px;
				margin-top: 20px;
			}
		}
		.separator {
			.set-default;
			padding: 50px 0;
			position: relative;
			z-index: 0;
			background-image: url('../images/separator.png');
			background-repeat: repeat;
			margin: 40px 0;
			.overlay {
				position: relative;
				z-index: 20;
			}
			.background {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: @dark-color;
				.opacity(.5);
				z-index: 5;
			}
		}
		.testimonial-wrap {
			.set-default;
			.bx-wrapper {
				background: transparent;
				.set-default;
				margin: 0;
			}
			.bx-viewport {
				background: transparent;
				border: none;
				.box-shadow(none);
			}
			.bx-pager {
				display: none;
			}
			.carousel-control {
				background: transparent;
				width: auto;
				.opacity(1);
				top: 50%;
				margin-top: -25px;
				font-size: 2.5rem;
				text-shadow: none;
				.transition(all .2s ease-in-out);
				cursor: pointer;
				&:hover {
					color: @light-color;
				}
				&.left {
					left: -40px;
				}
				&.right {
					right: -40px;
				}
			}
			.item {
				text-align: center;
				padding: 10px 0 10px 25px;
				.content {
					color: white;
					font-family: "Open Sans", "Arial, sans-serif";
					font-size: 1.5rem;
					font-weight: 300;
					.name {
						font-size: 1.6rem;
						font-weight: 400;
						margin-left: 10px;
					}
					&:before {
						content: '\e833';
						font-family: 'entypo';
						font-size: 4rem;
						color: white;
						display: inline-block;
						vertical-align: middle;
						margin-right: 10px;
						line-height: 2rem;
					}
				}
			}
		}
		.newsletter {
			.set-default;
			text-align: center;
			span {
				float: left;
				color: white;
				font-weight: 300;
				font-size: 1.5rem;
				font-family: "Open Sans", "Arial, sans-serif";
				margin-right: 20px;
			}
			input.form-control {
				float: left;
				width: 45%;
				margin-right: 20px;
				border-bottom: solid 2px white;
			    .placeholder(white);
				color: white;
				font-weight: 300;
				font-size: 1rem;
				font-family: "Open Sans", "Arial, sans-serif";
				margin-top: -9px;
			}
			.btn.btn-success.btn-empty {
				float: left;
				border-color: white;
				color: white;
				i {
					color: white;
				}
				&:hover {
					border-color: @alternate-color-2;
				}
			}
		}
		.service {
			.set-default;
			text-align: center;
			padding: 0 20px;
			margin-bottom: 50px;
			border: solid 1px transparent;
			position: relative;
			&:before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				bottom: 0;
				right: 0;
				background: @light-color;
				.opacity(0);
				.transition(~"all 0.4s ease-in-out");
			}
			&:hover {
				&:before {
					.opacity(0.1);
					top: -20px;
					bottom: -20px;
				}
			}
			.content {
				font-size: 1rem;
				line-height: 1.5;
				color: @text-light;
			}
			.pearl-icon {
				margin-bottom: 20px;
			}
		}
		.slider-wrap {
			&:hover {
				.carousel-control {
					.opacity(1);
				}
			}
			.set-default;
			position: relative;
			background: #333;
			.image-background {
				width: 100%;
				.background-size(cover);
				background-repeat: no-repeat;
				background-position: center center;
				height: 600px;
				position: relative;
				z-index: 1;
			}
			.overlay {
				background: @light-color;
				.opacity(.5);
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
